<template>
    <div class="swrapper" ref="swrapper">
    <div class="content">
    <slot></slot>
    </div>
    </div>
</template>

<script>
	import BScroll from "better-scroll"
    export default {
        name: "Scroll",
        data(){
        	return{
        		scroll:"null"
        	}
        },
        props:{
        	probeType :{
        		type:Number,
        		default: 0
        	},
        	pullUpLoad:{
        		type:Boolean,
        		default: false
        	}
        },
        mounted(){
        	//1.创建bscroll对象
        	this.scroll=new BScroll(this.$refs.swrapper,{
        		click:true,
        		probeType:this.probeType,
        		pullUpLoad:this.pullUpLoad
        	})
        	//2.监听滚动的位置
        	this.scroll.on('scroll',(Position)=>{
        		this.$emit('scroll',Position)
        	})
        	//3.监听scroll滚动到底部,pullingUp是一个事件名，不可以写错
        	if(this.pullUpLoad){
        		this.scroll.on('pullingUp', () => {
//      			console.log("KX")
        		this.$emit('pullingUp')
        	})
        	}
        },
        methods:{
        	scrolltop(x,y,time){
        	this.scroll && this.scroll.scrollTo(x,y,time);
        	},
        	refresh(){
//      		console.log("11")
        		this.scroll && this.scroll.refresh()
        	},
        	finishPullUp(){
        		this.scroll && this.scroll.finishPullUp()
        	},
        	getY(){
        		return this.scroll?this.scroll.y:0
        	}
        }
    }
</script>

